import { defineComponent, ref } from "vue";
import { Iphone, UserFilled } from "@element-plus/icons-vue";
import LoginPhone from "./LoginPhone.vue";
import LoginAccount from "./LoginAccount.vue";
export default defineComponent({
  setup() {
    // 账号登录实例
    const accountRef = ref(); // 手机登录实例

    const phoneRef = ref(); // 是否记住密码

    const pswStatus = ref(false); // 当前tab值

    const currentTab = ref("account"); // 提交

    const submit = () => {
      if (currentTab.value === "account") {
        accountRef.value?.submitForm(accountRef.value?.formRef, pswStatus.value);
      } else {
        phoneRef.value?.submitForm(phoneRef.value?.formRef, pswStatus.value);
      }
    };

    return {
      pswStatus,
      submit,
      accountRef,
      currentTab,
      phoneRef
    };
  },

  components: {
    Iphone,
    UserFilled,
    LoginAccount,
    LoginPhone
  }
});